
import XHeader from 'vux/src/components/x-header/index.vue'
import Search from 'vux/src/components/search/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
;
import mixins from 'assets/js/mixins.js'
export default {
	name: 'familyMembersearch',
	mixins: [mixins.addConcernApply],
	data() {
		return {
			loading: false,
			search: '', //查询输入的值				
		}
	},
	methods: {
		getData() {
			if(this.search != '') {
				this.loading = true;
				this.$common.ajax({
					result: true,
					data: {
						header: {
							action: 'GetUserInfo',
						},
						body: {
							userid: '',
							keyword: '',
							Mobile: this.search
						}
					},
					success: (res) => {
						this.loading = false;
						this.details = res
					},
					error: () => {
						this.loading = false;
					}
				})
			} else {
				this.$vux.toast.show({
					text: '请填写完整',
					type: 'text'
				});
			}
		}
		
	},
	mounted() {
	},

	components: {
		XHeader,
		Search,
		Spinner,
		XButton,
		Confirm,
		XTextarea
	}
}



