
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
;
import lrz from 'lrz' //图片压缩插件	
export default {
	name: 'realName',
	data() {
		return {
			submitData: {
				realName: '',
				idCard: '',
				certPhotoFront: '',
				certPhotoBack: ''
			}
		}
	},
	computed: {
		realState() {
			return this.$common.getLocalStorage('realState')
		}
	},
	methods: {
		//图片压缩并且转成base64字符串
		fileChange(e, opt) {
			lrz(e.target.files[0], {}).then((res) => {
				console.log(this.submitData[opt.type])
				this.submitData[opt.type] = res.base64;
			})
		},
		//提交表单
		submitForm() {
			this.$vux.confirm.show({
				title: '提示',
				content: '确认提交？',
				onConfirm: () => {
					this.$common.ajax({
						data: {
							header: {
								action: 'RealCertifiApply',
							},
							body: {
								userid: this.$common.getLocalStorage('user').userid,
								realname: this.submitData.realName,
								idcard: this.submitData.idCard,
								certphotofront: this.submitData.certPhotoFront.split(',')[1],
								CertPhotoBack: this.submitData.certPhotoBack.split(',')[1],
							}
						},
						success: (res) => {
							this.$vux.toast.show({
								text: '提交成功',
								onHide: () => {
									this.$router.go(-1);
								}
							});
						}
					})
				}
			})
		}
	},
	mounted() {
		this.$validator.validateAll()
	},
	components: {
		XButton,
		XHeader
	}
}



