
import XHeader from 'vux/src/components/x-header/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import xhFoot from 'components/footer';
import sos from 'components/sos'
export default {
	name: 'home',
	data() {
		return {
			news: {
				loading: true,
				list: [],
				page: 1,
				pageSize: 5,
				hasData: true,
			},
			banner: {
				active: 0,
				list: []
			}
		}
	},
	methods: {
		//获取海报
		getBanner() {
			this.$common.ajax({
				result: true,
				loading: false,
				data: {
					header: {
						action: 'GetAppAdvertisement',
					},
					body: {

					}
				},
				success: (res) => {
					res.forEach((t) => {
						this.banner.list.push({
							img: t.photo
						})
					})

				}
			})
		},
		//获取新闻列表
		getNews() {
			this.$common.ajax({
				result: true,
				loading: false,
				data: {
					header: {
						action: 'GetNewsListSingle',
					},
					body: {
						pageindex: this.news.page,
						pagesize: this.news.pageSize
					}
				},
				success: (res) => {
					this.news.loading = false;
					if(res.length > 0) {
						this.news.list = [...this.news.list, ...res];
					} else {
						this.news.hasData = false;
					}

				}
			})
		},
		//获取更多新闻
		getNewsMore() {
			this.news.page += 1
			this.getNews();
		}
	},
	mounted() {
		this.getNews();
		this.getBanner();
	},
	destroyed() {

	},

	components: {
		Swiper,
		XHeader,
		xhFoot,
		Spinner,
		sos

	}
}



