
	import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

	import sha512 from 'js-sha512' //sha512加密
	import mixins from 'assets/js/mixins.js'
	export default {
		name:'register',
		mixins: [mixins.city],
		data() {
			return {
				tel: '',
				vCode: '',
				password: '',
				password2: '',
				telLoading: false,
				vCodeStart: false, //验证码按钮开关
				time: 60, //验证码倒计时
				telNoRepeat: false //异步校验手机号码是否重复
			}
		},
		watch: {
			tel(opt) {
				if(opt.length == 11) {
					this.checkLoginName();
				}
			}
		},
		computed: {
			//判断两次密码是否相同
			isEqually() {
				return this.password == this.password2 ? true : false
			}

		},
		methods: {
			//验证号码是否被注册
			checkLoginName() {
				this.telLoading = true;
				this.telNoRepeat = false;
				this.$validator.validateAll({
					tel: this.tel
				}).then(() => {
					this.$common.ajax({
						loading:false,
						data: {
							header: {
								action: 'CheckMobile',
							},
							body: {
								mobile: this.tel,
							}
						},
						success: (res) => {
							this.telLoading = false;
							this.telNoRepeat = true;
						},
						error: (res) => {
							this.telLoading = false;
							this.$vux.toast.show({
								text: res.cause,
								type: 'cancel'
							});
						}
					})
				}).catch(() => {
					this.telLoading = false;
				});
			},
			//获取验证码
			getPhoneValidateCode() {
				this.$validator.validateAll({
					tel: this.tel
				}).then(() => {
					if(this.telNoRepeat) {
						this.$common.ajax({
							loadingText:'验证码发送中',
							data: {
								header: {
									action: 'SmsValidateCode',
								},
								body: {
									mobile: this.tel,
								}
							},
							success: (res) => {
								this.vCodeStart = true;
								this.$vux.toast.show({
									text: '验证码已发送至' + this.tel + '，请注意查收',
									width: '50%',
									type: 'text'
								})
							}
						})
					} else {
						this.$vux.toast.show({
							text: '该号码已注册，请直接登录',
							type: 'cancel'
						});
					}
				})
			},
			//验证码倒计时结束
			finish() {
				this.vCodeStart = false;
				this.time = 60;
			},
			//提交表单
			submitData() {
				if(this.telNoRepeat) {
						if(this.isEqually) {
							let data = {
								mobile: this.tel,
								validatecode: this.vCode,
								password: sha512(this.password)
							};
//							if(localStorage.openId != 'undefined') {
//								data.openId = localStorage.openId
//							}
							this.$common.ajax({
								data: {
									header: {
										action: 'register',
									},
									body: data
								},
								success: (res) => {
									this.$vux.toast.show({
										text: '注册成功',
										onHide: () => {
											this.$common.login({
												loading:true,
												tel: this.tel,
												password: sha512(this.password),
												render: () => {													
													if(!localStorage.cityList) {
														this.getCityList();
													}
													this.$router.push({
														path: '/homeRouterView/home'
													});
												}
											})
										}
									})
								}
							})
						} else {
							this.$vux.toast.show({
								text: '两次密码输入不一致',
								type: 'cancel'
							});
						}
					} else {
						this.$vux.toast.show({
							text: '该号码已注册，请直接登录',
							type: 'cancel'
						});
					}
			}
		},
		mounted() {
			this.$validator.validateAll()
		},
		components: {
			XInput,
			Group,
			XButton,
			XHeader,
			Countdown,
			Spinner
		}
	}



