
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
	name: 'message',
	data() {
		return {				
			details: {
				CheckState: 1
			}
		}
	},
	computed: {
		//地址传过来的参数
		locationSearch() {
			return this.$common.locationSearch()
		},
		//操作提交的数据的id字段名
		idType() {
			return this.details.Action == 'checkpatientsign'?'SignID':'ConcernID'
		}
	},
	methods: {
		getData() {
			this.$common.ajax({
				result: true,
				data: {
					header: {
						action: 'GetHealthWarnForWx',
					},
					body: this.locationSearch
				},
				success: (res) => {
					this.details = res;
				}
			})
		},
		operation(type) {
			this.$vux.confirm.show({
				title: '提示',
				content: '确认操作？',
				onConfirm: () => {	
					var bodyData={
						CheckState: type
					}						
					bodyData[this.idType]=this.details.ID;
					this.$common.ajax({
						result: true,
						data: {
							header: {
								action: this.details.Action
							},
							body: bodyData
						},
						success: (res) => {
							this.$vux.toast.show({
								text: '操作成功',
								onHide: () => {
									this.getData()
								}
							});
						}
					})
				}
			})

		}
	},
	mounted() {
		this.getData();

	},
	components: {
		XHeader,
		XButton
	}
}



