
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

export default {
	name: 'memberFamily',
	data() {
		return {
			show: false,
			list: [],
			loading: false
		}
	},
	computed: {},
	methods: {
		getData() {
			this.loading = true;
			setTimeout(() => {
				this.$common.ajax({
					loading: false,
					result: true,
					data: {
						header: {
							action: 'GetConcernList',
						},
						body: {
							userid: this.$common.getLocalStorage('user').userid,
							Type: '0'
						}
					},
					success: (res) => {
						this.loading = false;
						this.list = res;
					}
				})
			}, 500)
		},
		onHide(opt) {
			this.show = false;
			this.$emit('on-hide', opt);
		}
	},
	mounted() {},
	components: {
		XDialog,
		Spinner
	}
}



