
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import value2name from 'vux/src/filters/value2name.js'

import lrz from 'lrz' //图片压缩插件	
export default {
	data() {
		return {
			sexSelect: [{
				key: '-1',
				value: '未知'
			},{
				key: '1',
				value: '男'
			}, {
				key: '0',
				value: '女'
			}],
			userDetails: {},
			textareaShow: false
		}
	},
	computed: {
		//获取浏览器缓存LocalStorage里的城市json
		addressData(){
			return this.$common.getLocalStorage('cityList')
		},
		//处理数据返回的城市id
		addressSelect() {
			let str = String(this.userDetails.CityID).split('');
			return [
				str[0] + str[1] + '0000',
				str[0] + str[1] + str[2] + str[3] + '00',
				String(this.userDetails.CityID)
			]
		}
	},
	methods: {
		//模拟点击上传控件
		file() {
			this.$refs.file.click();
		},
		//图片压缩并且转成base64字符串
		fileChange(e) {
			lrz(e.target.files[0], {
				width: 200,
				height: 200,
				quality: 0.3
			}).then((res) => {
				this.userDetails.HeaderPhoto = res.base64;
			})
		},
		//获取用户信息
		getUserDetails() {
			this.$common.ajax({
				result: true,
				data: {
					header: {
						action: 'GetUserInfo',
					},
					body: {							
						userid: this.$common.getLocalStorage('user').userid
					}
				},
				success: (res) => {
					this.userDetails = res;
				}
			})
		},
		//提交表单
		setUserDetails() {
			this.$vux.confirm.show({
				title: '提示',
				content: '确认提交信息？',
				onConfirm: () => {
					let obj=JSON.parse(JSON.stringify( this.userDetails));
					obj.userid=obj.ID
					obj.CityID = this.$refs.addressSelect.currentValue[2];	
					if(obj.HeaderPhoto.indexOf('http') >= 0) {
						delete obj.HeaderPhoto
					} else {
						obj.HeaderPhoto = obj.HeaderPhoto.split(',')[1]
					}											
					this.$common.ajax({
						data: {
							header: {
								action: 'EditUserInfo',
							},
							body: obj
						},
						success: (res) => {
							localStorage.setItem('userPic',this.userDetails.HeaderPhoto);
							this.$vux.toast.show({
								text: '修改成功',
								onHide: () => {
									this.$router.go(-1);
								}
							});
						}
					})
				}
			})
		},
	},
	mounted() {
		this.getUserDetails();
		
	},		
	components: {
		Cell,
		Group,
		XInput,
		XAddress,
		XTextarea,
		Selector,
		PopupPicker,
		XHeader,
		Popup
	}
}



