
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
;
import lrz from 'lrz' //图片压缩插件		
export default {
	name: 'realName',
	data() {
		return {
			submitData: {
				realName: '',
				idCard: '',
				certPhotoFront: '',
				certPhotoBack: ''
			},
			hasFace:false//人脸识别
		}
	},
	computed: {
		realState() {
			return this.$common.getLocalStorage('realState')
		}
	},
	mounted() {

	},
	methods: {
		//图片压缩并且转成base64字符串
		fileChange(e, opt) {
			var d = document.querySelectorAll(".demo-container>.rect");
			for(var i = 0; i < d.length; i++) d[i].parentNode.removeChild(d[i]);
			lrz(e.target.files[0], {}).then((res) => {
				//					console.log(this.submitData[opt.type])
				this.submitData[opt.type] = res.base64;
			})
			setTimeout(() => {
				this.face();
			}, 500)
		},
		face() {
			var img = document.getElementById('img');
			var tracker = new tracking.ObjectTracker(['face', 'eye', 'mouth']);
			tracker.setStepSize(1.7);
			tracking.track('#img', tracker);
			tracker.on('track', (event) =>{
				if(!event.data.length) {
					this.hasFace=false;
					this.$vux.toast.show({
								text: '画面中没有人脸',
								type: 'cancel'
							});
				}else{						
					event.data.forEach((rect)=> {
					window.plot(rect.x, rect.y, rect.width, rect.height);
					this.hasFace=true;
				});
				}
				
			});

			window.plot = function(x, y, w, h) {
				var rect = document.createElement('div');
				document.querySelector('.demo-container').appendChild(rect);
				rect.classList.add('rect');
				rect.style.width = w + 'px';
				rect.style.height = h + 'px';
				rect.style.left = (img.offsetLeft + x) + 'px';
				rect.style.top = (img.offsetTop + y) + 'px';
			};
		},
		//提交表单
		submitForm() {
			this.$vux.confirm.show({
				title: '提示',
				content: '确认提交？',
				onConfirm: () => {
					this.$common.ajax({
						data: {
							header: {
								action: 'facerecognition',
							},
							body: {
								idcard: this.$route.query.idCard,
								baseimg: this.submitData.certPhotoFront.split(',')[1]
							}
						},
						success: (res) => {
							this.$vux.toast.show({
								text: '提交成功',
								onHide: () => {
									this.$router.go(-1);
								}
							});
						}
					})
				}
			})
		}
	},
	components: {
		XButton,
		XHeader
	}
}



