
import XHeader from 'vux/src/components/x-header/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

export default {

	data() {
		return {
			type:0 ,//0家庭成员1监护人
			loading: true,
			list: []
		}
	},
	methods: {
		getData(opt) {
			this.type=opt.type;
			this.loading = true;
			this.$common.ajax({
				loading: false,
				result: true,
				data: {
					header: {
						action: 'GetConcernList',
					},
					body: {
						userid: this.$common.getLocalStorage('user').userid,
						Type: this.type
					}
				},
				success: (res) => {
					this.loading = false;
					this.list = res;
				}
			})
		}

	},
	mounted() {
		this.getData({type:'0'});
	},

	components: {
		XHeader,
		Spinner,
		ButtonTab,
		ButtonTabItem
	}
}



