
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import xhFoot from 'components/footer';
export default {
	data() {
		return {
			eqiupBind: [], //设备绑定列表
			details: {}, //用户信息
			follow: {
				loading: true,
				count: ''
			},
			guardian: {
				loading: true,
				count: ''
			}
		}
	},
	computed: {
		//绑定状态
		eqiupState() {
			return this.eqiupBind.length > 0 ? true : false
		}
	},
	methods: {
		//获取绑定设备
		getEqiupBind() {
			this.$common.ajax({
				loading: false,
				result: true,
				data: {
					header: {
						action: 'GetEqiupBindList',
					},
					body: {
						userid: this.$common.getLocalStorage('user').userid,
						equiptype: 101
					}
				},
				success: (res) => {
					this.eqiupBind = res;
				}
			})
		},
		//获取个人资料
		getData() {
			this.$common.ajax({
				loading: false,
				result: true,
				data: {
					header: {
						action: 'GetUserInfo',
					},
					body: {
						userid: this.$common.getLocalStorage('user').userid
					}
				},
				success: (res) => {
					this.details = res;
					this.$common.setLocalStorage('realState', this.details.RealState);
				}
			})
		},
		//获取关注人数
		getConcernCount(opt) {
			this.$common.ajax({
				loading: false,
				result: true,
				data: {
					header: {
						action: 'GetConcernCount',
					},
					body: {
						userid: this.$common.getLocalStorage('user').userid,
						checkstate: opt.type
					}
				},
				success: (res) => {
					this[opt.name].loading = false;
					this[opt.name].count = res.count;
				}
			})
		},
		//解除绑定
		logout() {
			this.$vux.confirm.show({
				title: '提示',
				content: '确认解除绑定？',
				onConfirm: () => {
					this.$common.ajax({
						loadingText: '解除中',
						data: {
							header: {
								action: 'WxUnbind',
							},
							body: {
								usertype:1,
								openid: localStorage.openId
							}
						},
						success: (res) => {
							this.$common.clearLocalStorage();								
							this.$router.push({
								path: '/login'
							});
						}
					})
				}
			})
		}
	},
	mounted() {
		this.getData();
		this.getConcernCount({
			name: 'follow',
			type: 0
		});
		this.getConcernCount({
			name: 'guardian',
			type: 1
		});
		this.getEqiupBind();
	},

	components: {
		Group,
		Cell,
		xhFoot,
		XHeader,
		XButton,
		Spinner
	}
}



