
	import XHeader from 'vux/src/components/x-header/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'

	export default {
		name: 'hospital',
		data() {
			return {
				addressLoading: false,
				loading: true,
				location: {},//百度api获取的位置
				search: '',
				list: [],				
			}
		},
		computed: {
			//获取浏览器缓存LocalStorage里的城市json
			addressData() {
				return this.$common.getLocalStorage('cityList')
			},
			//城市编码ID
			addressSelect() {
				let province =_.find(this.addressData,(t)=>{
					return t.name==this.location.address.province
				}).value
				let city =_.find(this.addressData,(t)=>{
					return t.name==this.location.address.city
				}).value
				let district =_.find(this.addressData,(t)=>{
					return t.name==this.location.address.district
				}).value
				return [province,city,district]
//				return [province]
			}
		},
		watch:{
			
		},
		methods: {
			//替换右上角地址文字
			replaceText(){
				this.location.address.district=this.$refs.addressSelect.nameValue.split(' ')[2];
				this.getData();
			},
			//获取医院列表
			getData(opt) {
				this.loading = true;
				this.$common.ajax({
					loading: false,
					result: true,
					data: {
						header: {
							action: 'GetHospitalList',
						},
						body: {
							KeyWord: this.search,
							CityID: opt&&opt.search?'':this.addressSelect[2],
							Level: '',
							IsMedicare: '',
							Prop: '',
						}
					},
					success: (res) => {
						this.loading = false;
						this.list = res;
					}
				});
			},
			//获取定位
			getLocation() {
				this.addressLoading = true
				new BMap.Geolocation().getCurrentPosition((location) => {
					this.location = location;
					if(location.address.district==''){
						this.location.address.district='鹿城区'
					}
					this.addressLoading = false
					this.getData();
				})
			}

		},
		mounted() {
			this.getLocation();
		},

		components: {
			XHeader,
			Spinner,
			XAddress
		}
	}



