
import XHeader from 'vux/src/components/x-header/index.vue'
	
export default {
	data() {
		return {
			
		}
	},
	computed: {			
		direction(){
			return this.$store.state.direction
		},
		headerTransition() {
			return this.direction === 'forward' ? 'vux-header-fade-in-right' : 'vux-header-fade-in-left'
		}
	},
	methods: {},
	mounted() {
		
	},

	components: {
		XHeader

	}
}



