
import XHeader from 'vux/src/components/x-header/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
	name: 'visitDetails',
	data() {
		return {
			
		}
	},
	computed: {
		isComment(){
			return this.$route.query.details.CommentID!='0'?true:false
		},
		details() {
			return this.$route.query.details
		},
		submitData(){
				return{
					VisitID:this.$route.query.details.ID,
					DoctorID:this.$route.query.details.DoctorID,
					UserID:this.$common.getLocalStorage('user').userid,
					Content:this.isComment?this.$route.query.details.CommentContent:'',
					Star:this.isComment?parseFloat(this.$route.query.details.CommentStar):0,
				}
			}
	},
	methods: {
		addComment(){
			if(this.submitData.Content!='') {
				this.$vux.confirm.show({
					title: '提示',
					content: '确认提交？',
					onConfirm: () => {
						this.$common.ajax({
							data: {
								header: {
									action: 'AddComment',
								},
								body: this.submitData
							},
							success: (res) => {
								this.$vux.toast.show({
									text: '评论成功',
									onHide: () => {
										this.$router.go(-1);
									}
								});
							}
						})
					}
				})
			} else {
				this.$vux.toast.show({
					text: '评论内容不能为空',
					type: 'text'
				});
			}
		},
		deleteComment(){
			this.$vux.confirm.show({
					title: '提示',
					content: '确认删除？',
					onConfirm: () => {
						this.$common.ajax({
							data: {
								header: {
									action: 'DeleteComment',
								},
								body: {
									CommentID:this.$route.query.details.CommentID
								}
							},
							success: (res) => {
								this.$vux.toast.show({
									text: '删除成功',
									onHide: () => {
										this.$router.go(-1);
									}
								});
							}
						})
					}
				})
		},
	},
	mounted() {
	},

	components: {
		XHeader,Rater,Group, Cell ,XTextarea ,XButton
		

	}
}



