
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

export default {
	name: 'contractDoctorDetails',
	data() {
		return {
			confirm: false,
			details: {}, //医生信息
			doctorEvaluation: '', //医生评价数量
			applyText: '' //备注
		}
	},
	computed: {
		//是否签约 0未签约1已签约
		isSign() {
			if(this.$route.query.isSign == 0) {
				return false
			} else {
				return true
			}
		}
	},
	methods: {
		//获取医生的评价数量
		getCommentStat() {
			this.$common.ajax({
				result: true,
				data: {
					header: {
						action: 'GetCommentStat',
					},
					body: {
						DoctorID: this.$route.query.id
					}
				},
				success: (res) => {
					this.doctorEvaluation = res.TotalCount;
				}
			})
		},
		getData() {
			this.$common.ajax({
				result: true,
				data: {
					header: {
						action: 'GetDoctorInfo',
					},
					body: {
						DoctorID: this.$route.query.id,
						mobile: '',
						keyword: ''
					}
				},
				success: (res) => {
					this.details = res;
					this.details.ServiceStar = parseFloat(this.details.ServiceStar)

				}
			})
		},
		//签约医生
		addPatientSignApply() {
			this.$common.ajax({
				data: {
					header: {
						action: 'AddPatientSignApply',
					},
					body: {
						DoctorID: this.$route.query.id,
						UserID: this.$common.getLocalStorage('user').userid,
						Direction: 1,
						ApplyText: this.applyText
					}
				},
				success: (res) => {
					this.$vux.toast.show({
						text: '签约请求已发送'
					});

				}
			})
		},
		//解除签约
		deletePatientSign() {
			this.$common.ajax({
				data: {
					header: {
						action: 'DeletePatientSign',
					},
					body: {
						DoctorID: this.$route.query.id,
						UserID: this.$common.getLocalStorage('user').userid,
						UnSignDirection: '1',
						UnSignText: this.applyText
					}
				},
				success: (res) => {
					this.$vux.toast.show({
						text: '解除签约请求已发送'
					});

				}
			})
		},
		onConfirm() {
			this.isSign ? this.deletePatientSign() : this.addPatientSignApply()
		}
	},
	mounted() {
		this.getData();
		this.getCommentStat();
	},

	components: {
		XHeader,
		Group,
		Cell,
		Rater,
		Confirm,
		XTextarea
	}
}



