
import XHeader from 'vux/src/components/x-header/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

import mixins from 'assets/js/mixins.js'
export default {
	name: 'appointment',
	mixins: [mixins.scroller],
	data() {
		return {
			state: {
				0: 'warn', //未就诊
				1: 'success', //已就诊
				2: 'overdue' //过期
			}
		}
	},
	computed: {
		ajaxData() {
			return {
				header: {
					action: 'GetAppointmentList',
				},
				body: {
					UserID: this.$common.getLocalStorage('user').userid
				}
			}
		}
	},
	methods: {
		//取消预约
		deleteAppointment(opt,index) {
			this.$vux.confirm.show({
				title: '提示',
				content:'确认取消预约？',
				onConfirm: () => {
					this.$common.ajax({
						data: {
							header: {
								action: 'DeleteAppointment',
							},
							body: {
								AppointmentID:opt.ID
							}
						},
						success: (res) => {
							this.list.splice(index,1);
						}
					})

				}
			})

		}
	},
	mounted() {},
	components: {
		XHeader,
		Spinner
	}
}



