
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

export default {
	data() {
		return {
			loading: false,
			list: [],
		}
	},
	methods: {
		getData() {
			this.loading=true;
			this.$common.ajax({
				loading: false,
				result: true,
				data: {
					header: {
						action: 'GetDepartList',
					},
					body: {
						HospitalID: this.$route.query.id,
						ParentID: '',
						KeyWord: ''
					}
				},
				success: (res) => {
					this.loading=false;
					this.list = res;
				}
			})
		},
	},
	mounted() {
		this.getData();
	},

	components: {
		XHeader,
		Divider,
		Spinner
	}
}



