
import XHeader from 'vux/src/components/x-header/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import mixins from 'assets/js/mixins.js'
export default {
	name: 'familyMemberDetails',
	mixins: [mixins.addConcernApply],
	data() {
		return {

		}
	},
	computed: {
		//类型
		type() {
			return this.$route.query.type
		}
	},
	methods: {
		//关注取消关注切换
		submitSelect() {
			this.type == '0' ? this.deteleData() : this.confirm = true
		},
		deteleData() {
			this.$vux.confirm.show({
				title: '提示',
				content: '确认删除关注？',
				onConfirm: () => {
					this.$common.ajax({
						data: {
							header: {
								action: 'DeleteConcern',
							},
							body: {
								ConcernID: this.$route.query.concernId,
								DeleteUserID: this.$common.getLocalStorage('user').userid,
							}
						},
						success: (res) => {
							this.$vux.toast.show({
								text: '取消关注成功',
								type: 'success',
								onHide:() => {
									this.$router.push({
										path: '/homeRouterView/familyMember'
									})
								}
							})
						}
					})
				}
			});
		},
		getData() {
			this.$common.ajax({
				result: true,
				data: {
					header: {
						action: 'GetUserInfo',
					},
					body: {
						userid: this.$route.query.id
					}
				},
				success: (res) => {
					this.details = res;
				}
			})
		},
	},
	mounted() {
		this.getData();
	},

	components: {
		XHeader,
		Confirm,
		XTextarea
	}
}



