
import XHeader from 'vux/src/components/x-header/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'

export default {
	data() {
		return {
			loading: false,
			details: {}
		}
	},
	computed:{
		
	},
	methods: {
		//获取新闻
		getNews() {
			this.loading = true;
			this.$common.ajax({
				result: true,
				loading: false,
				data: {
					header: {
						action: 'GetNewsInfo',
					},
					body: {
						id: this.$route.query.id
					}
				},
				success: (res) => {
					this.loading = false;
					this.details = res;
				}
			})
		}
	},
	mounted() {
		this.getNews();
	},
	destroyed() {

	},

	components: {
		XHeader,
		Spinner

	}
}



