
import XHeader from 'vux/src/components/x-header/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import mixins from 'assets/js/mixins.js'
export default {
	name: 'contractDoctorEveluate',
	mixins: [mixins.scroller],
	data() {
		return {				
			doctorEvaluation: {}
		}
	},
	computed: {			
		ajaxData() {
			return {
				header: {
					action: 'GetCommentList',
				},
				body: {
					DoctorID: this.$route.query.id
				}
			}
		}
	},
	methods: {
		getCommentStat() {
			this.$common.ajax({
				result: true,
				data: {
					header: {
						action: 'GetCommentStat',
					},
					body: {
						DoctorID: this.$route.query.id
					}
				},
				success: (res) => {
					this.doctorEvaluation = res;

				}
			})
		}

	},
	mounted() {
		this.getCommentStat();
	},
	components: {
		XHeader,
		Rater,
		Spinner,
		Group,
		Cell
	}
}



